@use 'base/variables' as v;
@use 'base/mixins' as m;

body {
    background-color: v.$colorFondoBody;
}

.cuerpo {
    overflow-x: hidden;
}

.colorTitulo {
    color: v.$colorTitulo;
    font-family: v.$fuenteTitulo;
}

.colorTituloDos {
    color: v.$colorTituloDos;
    font-family: v.$fuentePrincipal;
}

.colorLinks {
    color: v.$colorLinks;
    font-family: v.$fuentePrincipal;
    font-size: 1rem;
}

.colorSubTitulo {
    color: v.$colorSubTitulo;
    font-family: v.$fuentePrincipal;
}

.colorParrafo {
    color: v.$colorParrafo;
    font-family: v.$fuenteParrafo;
}

.colorParrafoDos {
    color: v.$colorParrafoDos;
    font-family: v.$fuenteParrafo;
}

.colorParrafoTres {
    color: v.$colorParrafoTres;
    font-family: v.$fuenteParrafo;
}

.colorPaCopyright {
    color: v.$colorPaCopyright;
    font-family: v.$fuentePrincipal;
}

.btnGeneral,
.btnPortfolio {
    padding: .8rem 1rem;
    border-radius: 4px;
    border: none;

    &:hover {
        cursor: pointer;
    }
}

.btnPortfolio {
    padding: .4rem 1rem;
}

[class*="__H1"] {
    font-size: 2.5rem;
    font-weight: 500;
    padding: 0;
    margin: 0;
}

[class*="__H2"] {
    font-size: 2.2rem;
    font-weight: 500;
    line-height: 1.3;
    padding: 0;
    margin: 0;
}

[class*="__H3"] {
    font-size: 2rem;
    font-weight: 500;
    padding: 0;
    margin: 0;
}

[class*="__H4"] {
    font-size: 1.25rem;
    font-weight: 500;
    line-height: 1.5;
    padding: 0;
    margin: 0;
}

[class*="__H5"] {
    font-size: 1.2rem;
    font-weight: 600;
    line-height: 1.5;
    padding: 0;
}

p {
    margin-top: 0;
    margin-bottom: 1.4rem;
}