@use 'base/variables' as v;
@use 'base/mixins' as m;

.informacion {
    display: flex;
    flex-direction: row;
    position: relative;
    transition: right .7s ease;
    right: 0%;

    &.right100 {
        right: 100%;
    }

    &.right200 {
        right: 200%;
    }

    &.right300 {
        right: 300%;
    }

}

.informacionSobremi,
.informacionServicio,
.informacionPortafolio {
    background-color: #fff;
    min-width: 85%;
    width: 85%;
    position: relative;
    margin: 0 7.5%;
    border-radius: .4rem;
    padding-bottom: 4rem;
    margin-top: -12rem;
    margin-bottom: 4rem;

    &__plantillaSobreGeneral {
        padding: 5rem 4rem 0rem 4rem;
    }

    &__plantillaSobre {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        gap: 1rem;
        width: 100%;

        @include m.tablet {
            width: 85%;
        }

        margin: 0 auto;
        line-height: 1.6;
    }

    &__sobremiDatos {
        padding-bottom: 2rem;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        border-bottom: 1px solid v.$grisClaro;

        @include m.tablet {

            display: grid;
            grid-template-columns: repeat(2, 1fr);
        }
    }

    &__H4 {
        margin-top: 0;
        margin-bottom: 1rem;
    }

    &__H5 {
        margin: 0;
    }

    &__Skills {
        display: flex;
        flex-direction: column;
        gap: 0.5rem 2rem;
        padding-bottom: 4rem;
        border-bottom: 1px solid v.$grisClaro;

        @include m.tablet {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
        }
    }

    &__experiencia {
        padding: 5rem 4rem 0rem 4rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 3rem;
    }

    // &__boxExperienciaTitulo {}

    &__boxExperienciaDetalles {
        display: flex;
        flex-direction: column;
    }

    &__boxExperienciaArticuloGrid {
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        @include m.tablet {
            display: grid;
            grid-template-columns: 1fr 2fr;
            gap: 1rem;
        }
    }

    &__boxExperienciaArticulo {
        margin-bottom: .4rem;

        & .informacion__H4 {
            padding: 0;
            margin: 0;
            font-size: 1rem;
        }
    }

    &__cv {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 2rem;
        background-color: v.$colorFondoBtn;
        padding: 80px 0;
        width: 90%;
        margin: 0 auto 0rem auto;
    }

    // &__cvTitulo {}

    &__cvDescarga {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }

    &__plantillaServicios {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-row-start: 1fr 1fr;
        gap: 3rem;

        h5 {
            margin-bottom: .6rem;
        }

        span {
            padding: 0.6rem;
            border-radius: 2px;
            margin-bottom: 1rem;

            &#contact {
                background-color: #E9ECFE;
                color: #AAB6FC;
            }

            &#bolt {
                background-color: #F1F2F4;
                color: #7D8999;
            }

            &#brush {
                background-color: #FFEAED;
                color: #FF3755;
            }

            &#divices {
                background-color: #E8FAEE;
                color: #35D26A;
            }
        }

        & .material-symbols-outlined {
            font-variation-settings:
                'FILL' 0,
                'wght' 400,
                'GRAD' 0,
                'opsz' 48;
        }
    }

    &__muestrasGrid {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(6, auto);
        height: auto;
        width: 110%;
        gap: 1rem;
    }

    &__muestrasBox {
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: .1rem;
        border-radius: 4px;

        &:hover {
            cursor: pointer;
        }

        p.informacionPortafolio__parrafo {
            text-align: start;
            margin-bottom: 0;
        }

        h4 {
            text-align: start;
        }
    }

    &__imagenPortafolioBox {
        overflow: hidden;
        height: 15rem;
        max-height: min-content;
        border-radius: 4px;
    }

    &__imagenPortafolio {
        width: 100%;
        height: 100%;
        border-radius: 4px;
        transition: transform .4s ease-in-out;

        &:hover {
            transform: scale(1.2);
        }
    }
}

#BtnDownload {
    background-color: v.$colorBtn;
}

.barra {
    display: grid;
    grid-template-columns: 1fr 1px;
    align-items: center;
    column-gap: 1rem;

    margin: -.5rem 0;
    padding: 0;
    border-radius: 4px;
    background-color: lighten(v.$grisClaro, 10);

    &.temaClaro {
        background-color: darken(v.$colorBtn, 20);
    }

    :root {
        --habilidades-bar-lenght: 0%;
    }

    &__bar,
    &__bar::after {
        position: relative;
        background-color: v.$grisClaro;
        width: 100%;
        height: 5px;
        // border-radius: 8px;
        overflow: hidden;
    }

    &__bar::after {
        content: "";
        position: absolute;
        left: -100%;
        background-color: v.$colorBtn;
        transition: 0.3s ease-in-out;
        animation: habilidades-barra 3s ease-in-out forwards;
    }

    &__bar--0::after {
        --habilidades-bar-lenght: 0%;
    }

    &__bar--5::after {
        --habilidades-bar-lenght: 5%;
    }

    &__bar--10::after {
        --habilidades-bar-lenght: 10%;
    }

    &__bar--15::after {
        --habilidades-bar-lenght: 15%;
    }

    &__bar--20::after {
        --habilidades-bar-lenght: 20%;
    }

    &__bar--25::after {
        --habilidades-bar-lenght: 25%;
    }

    &__bar--30::after {
        --habilidades-bar-lenght: 30%;
    }

    &__bar--35::after {
        --habilidades-bar-lenght: 35%;
    }

    &__bar--40::after {
        --habilidades-bar-lenght: 40%;
    }

    &__bar--45::after {
        --habilidades-bar-lenght: 45%;
    }

    &__bar--50::after {
        --habilidades-bar-lenght: 50%;
    }

    &__bar--55::after {
        --habilidades-bar-lenght: 55%;
    }

    &__bar--60::after {
        --habilidades-bar-lenght: 60%;
    }

    &__bar--65::after {
        --habilidades-bar-lenght: 65%;
    }

    &__bar--70::after {
        --habilidades-bar-lenght: 70%;
    }

    &__bar--75::after {
        --habilidades-bar-lenght: 75%;
    }

    &__bar--80::after {
        --habilidades-bar-lenght: 80%;
    }

    &__bar--85::after {
        --habilidades-bar-lenght: 85%;
    }

    &__bar--90::after {
        --habilidades-bar-lenght: 90%;
    }

    &__bar--95::after {
        --habilidades-bar-lenght: 95%;
    }

    &__bar--100::after {
        --habilidades-bar-lenght: 100%;
    }
}

@keyframes habilidades-barra {
    to {
        transform: translateX(var(--habilidades-bar-lenght))
    }
}

.ocultar {
    margin-top: -1000rem;
    z-index: -1;
    height: 0;
}

.disp {
    transition: transform .3s ease-in-out;
    transform: scale(0);
    opacity: .7;
}

.bloc {
    transition: transform .3s ease-in-out;
    transform: scale(1);
    opacity: 1;
}

.scroll {
    overflow-y: hidden;
}