@use 'base/variables' as v;
@use 'base/mixins' as m;

.header__navegador {
    display: none;

    &.activo {
        display: flex;
    }

    flex-direction: column;
    align-items: center;
    margin: 0 auto 0 auto;
    justify-content: space-around;
    gap: .5rem;

    span {
        color: v.$colorParrafo;
        font-weight: 500;

        &:hover {
            color: v.$colorBtn;
            cursor: pointer;
        }
    }

    button {
        background-color: unset;
        border: unset;
    }

    @include m.tablet {
        margin: 0;
        flex-direction: row;

        span {
            color: v.$colorLinks;
            font-weight: 500;

            &:hover {
                color: v.$colorLinks;
                cursor: pointer;
            }
        }

        &Opcion {
            padding: .2rem .4rem;
            position: relative;

            &:hover {
                cursor: pointer;
            }

            &.activo {
                &::after {
                    content: "";
                    display: block;
                    position: absolute;
                    top: 2rem;
                    width: 1.75rem;
                    margin-left: auto;
                    margin-right: auto;
                    bottom: 0.725rem;
                    left: 0.75rem;
                    right: 0.75rem;
                    border-bottom: 3px solid currentColor;
                }
            }
        }
    }


}