@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
    padding: .8rem .6rem;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    width: 100%;
    height: max-content;

    &.activo {
        background-color: white;
    }

    @include m.tablet {
        flex-direction: row;
        background-color: transparent;
        width: auto;
    }

}